@import '../../styles/_variables.scss';

.bountiesList {
  display: flex;
  flex-direction: column;
  text-align: left;

  .muted {
    color: $text-color-muted;
  }
}

.bounty {
  display: flex;
  flex-direction: column;
  background-color: $bounty-background-color;
  margin-bottom: 0.5rem;
  border-radius: 5px;
  padding: 0.5rem;

  h3, p { margin: 0; }

  .header {
    display: flex;

    .left {
      width: 80%;
      text-align: left;
    };

    .right {
      width: 20%;
      text-align: right;
    }

    .title {
      color: $bounty-title-color; 
    }

    .amount {
      color: $bounty-amount-color; 
    }
  };

  .body {
    text-align: left;
  };
}